import { OrderDataModel, OrdersReturnPropsModel } from '@/order/data/models';
import { UseOrderReturnsProps } from '@/order/types';
import { events } from '@adobe-commerce/event-bus';
import { useState, useEffect } from 'preact/hooks';

export const useOrderReturns = ({ orderData }: UseOrderReturnsProps) => {
  const [order, setOrder] = useState(orderData);
  const [orderReturns, setOrderReturns] = useState<
    OrdersReturnPropsModel[] | []
  >([]);

  useEffect(() => {
    const event = events.on(
      'order/data',
      (order: OrderDataModel) => {
        setOrder(order);
        setOrderReturns(order?.returns);
      },
      { eager: true }
    );
    return () => {
      event?.off();
    };
  }, []);

  return { order, orderReturns };
};
